"use client";

import { useState, useEffect } from 'react';
import { DashboardLayout } from '@/components/layout/DashboardLayout';
import { ProjectTable } from '@/components/projects/ProjectTable';
import { ProjectTableSkeleton } from '@/components/projects/ProjectTable'; // Create this
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Search } from 'lucide-react';
import { useRouter } from 'next/navigation';

export default function ProjectsPage() {
  const [projects, setProjects] = useState([]);
  const [loading, setLoading] = useState(true);
  const [departments, setDepartments] = useState([]);
  
  // Filters
  const [search, setSearch] = useState('');
  const [deptFilter, setDeptFilter] = useState('all');
  const [yearFilter, setYearFilter] = useState('all');
  const [statusFilter, setStatusFilter] = useState('all');

  const router = useRouter();

  useEffect(() => {
    // Fetch departments for the filter dropdown
    fetch('/api/departments').then(res => res.json()).then(setDepartments);
  }, []);

  useEffect(() => {
    const fetchProjects = async () => {
      setLoading(true);
      const query = new URLSearchParams({
        search,
        departmentId: deptFilter,
        year: yearFilter,
        status: statusFilter
      });
      const res = await fetch(`/api/projects?${query.toString()}`);
      const data = await res.json();
      setProjects(data);
      setLoading(false);
    };

    const debounce = setTimeout(fetchProjects, 300);
    return () => clearTimeout(debounce);
  }, [search, deptFilter, yearFilter, statusFilter]);

  return (
    <DashboardLayout title="Projects" currentPath="/projects" onNavigate={(path) => router.push(path)}>
      <div className="space-y-6">
        {/* Filter Bar */}
        <div className="flex flex-wrap gap-4 items-center glass-card p-4 rounded-xl">
          <div className="relative flex-1 min-w-[200px]">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-muted-foreground" />
            <Input 
              placeholder="Search project or client..." 
              className="pl-9" 
              value={search}
              onChange={(e) => setSearch(e.target.value)}
            />
          </div>
          <Select value={deptFilter} onValueChange={setDeptFilter}>
            <SelectTrigger className="w-48"><SelectValue placeholder="Department" /></SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Departments</SelectItem>
              {departments.map((d: any) => <SelectItem key={d.id} value={d.id.toString()}>{d.name}</SelectItem>)}
            </SelectContent>
          </Select>
          <Select value={statusFilter} onValueChange={setStatusFilter}>
            <SelectTrigger className="w-40"><SelectValue placeholder="Status" /></SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Status</SelectItem>
              <SelectItem value="ONGOING">Ongoing</SelectItem>
              <SelectItem value="COMPLETED">Completed</SelectItem>
            </SelectContent>
          </Select>
          <Select value={yearFilter} onValueChange={setYearFilter}>
            <SelectTrigger className="w-32"><SelectValue placeholder="Year" /></SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Years</SelectItem>
              {[2024, 2025, 2026].map(y => <SelectItem key={y} value={y.toString()}>{y}</SelectItem>)}
            </SelectContent>
          </Select>
        </div>

        {loading ? (
          <ProjectTableSkeleton />
        ) : (
          <ProjectTable 
            projects={projects.map((p: any) => ({
              ...p,
              name: p.projectName,
              clientName: p.client.name,
              totalBudget: Number(p.totalProjectValue),
              signDate: p.startDate,
              status: p.bills.every((b: any) => b.status === 'PAID') ? 'COMPLETED' : 'ONGOING'
            }))} 
            onProjectClick={(p) => router.push(`/projects/${p.id}`)}
          />
        )}
      </div>
    </DashboardLayout>
  );
}